import React, { Component, Suspense } from 'react';
import { baseUrl, FontAwesomeIcon } from '../../../Assets/AssetsHelper'
import { Dropdown, Navbar, Button, Image } from 'react-bootstrap';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import ContentSwitcher from '../Main/CommonComponents/ContentSwitcher';
import { Cookies } from 'react-cookie';
import { OPEN_INTERCOM } from '../../../utils';
const UserDropdown = React.lazy(() => import('./UserDropdown'));
const Notifications = React.lazy(() => import('../Main/CommonComponents/Notifications'));
const cookies = new Cookies();


const DropdownItem = (props) => {
       return <Dropdown.Item {...props}>
              <FontAwesomeIcon type={'solid'} icon={props.iconText} />
              {props.text}
       </Dropdown.Item>
}

class Header extends Component {
       constructor(props) {
              super(props);
              this.state = { isActiveAside: false, appThemeSkin: cookies.get('appThemeSkin') ? cookies.get('appThemeSkin') == 'light' ? true : false : true, isNotification: false };
              if (cookies.get('appThemeSkin')) {
                     const htmlTag = document.getElementsByTagName("html")[0];
                     htmlTag.setAttribute("data-theme", cookies.get('appThemeSkin'));
              }
       }
    
       setMode = () => {
              const htmlTag = document.getElementsByTagName("html")[0];
              this.setState({ appThemeSkin: !this.state.appThemeSkin }
              ,() => {
                     htmlTag.setAttribute("data-theme", this.state.appThemeSkin ? 'light' : 'dark')
                     cookies.set("appThemeSkin", this.state.appThemeSkin ? 'light' : 'dark', { path: '/' })
              }
              );

       };
       componentDidMount() {
              
       }

       onNotificationToggle = () => this.setState({ isNotification: !this.state.isNotification })
       render() {
              const { navigate, authorized, notificationList} = this.props
              const { isNotification, appThemeSkin } = this.state
              const notifications_count = notificationList.filter((a) => a.status == 0).length

              return <Navbar className="header-main fixed-top f-center border-bottom py-0 bg-body flex-nowrap" expand="lg">
                     <div className="app-logo f-center flex-shrink-1 border-end">
                            <Link to={"/"} className="f-center header_logo">
                                   <span className="d-flex w-100">
                                          <Image src={baseUrl('media/logo/tagshop-icon.svg')} width="44" height="44" alt='Tagshop' fluid/>
                                   </span>
                            </Link>
                     </div>
                     <div className="app-container f-between flex-grow-1 h-100">
                            <div className="d-flex flex-grow-1 w-75">
                                   <div className="d-lg-none d-flex align-items-center justify-content-center mob_toggle_icon me-3">
                                          <Button id="main_menu_toggle" variant='icon' size='sm' className="p-0 custom_burger_icon bg-tansparent">
                                                 <span></span>
                                                 <span></span>
                                                 <span></span>
                                          </Button>
                                   </div>
                                   <div className='w-100 header_swticher'>
                                          {/\/(moderation|addfeed|managefeed|more-apps|manual-upload|reviewhub)/.test(window.location.pathname) ? <ContentSwitcher header={true}/> : ''}
                                   </div>
                            </div>
                            <div className="header-menu d-flex align-items-center justify-content-end w-xs-100 h-100">
                                   <div className="d-flex align-items-center h-100 flex-shrink-0">
                                          {authorized?.userData && !authorized?.userData?.user_data?.UserPlan?.planId?.includes('39') && authorized?.userData?.user_data?.collaborator == '' &&(
                                                 <Link to={`/price`} className="btn btn-primary text-nowrap btn-sm mx-1 d-none d-sm-flex" style={{ minWidth: 34 }}>
                                                        Upgrade Now!
                                                 </Link>
                                          )}

                                          <Button variant="icon" size='sm' className="fs-6 border-0 ms-sm-1" onClick={this.setMode} aria-label="Theme Mode">
                                                 <i className={`fa-duotone fa-solid fa-${appThemeSkin ? 'moon' : 'sun-bright'}`}></i>
                                          </Button>

                                          <Dropdown className='h-100 f-center' align="end">
                                                 <Dropdown.Toggle variant='icon' size='sm' className='fs-6 border-0 arrow_disabled ms-sm-1' aria-label="Support">
                                                        <i className="fa-duotone fa-solid fa-circle-question"></i>
                                                 </Dropdown.Toggle>
                                                 <Dropdown.Menu>
                                                        <DropdownItem iconText="comments" text="Chat" onClick={OPEN_INTERCOM} />
                                                        <DropdownItem iconText="phone" text="Call" href="https://calendly.com/taggbox/csm" target='_blank' />
                                                        <DropdownItem iconText="envelope" text="Email" href="mailto:support@tagshop.ai" target='_blank' />
                                                        <DropdownItem iconText="book-open-cover" text="Knowledge Base" href="https://tagshop.ai/support/" target='_blank' />
                                                 </Dropdown.Menu>
                                          </Dropdown>
                                          <Dropdown className="head_notifications_ h-100 f-center" align="end" show={isNotification} onToggle={this.onNotificationToggle}>
                                                 <Dropdown.Toggle variant="icon" size='sm' className="fs-6 border-0 arrow_disabled ms-sm-1 position-relative" aria-label="Notifications">
                                                        {notifications_count ? <span className="notifications_count bg-primary text-white position-absolute rounded-4" id="notificationCount" style={{zIndex:9}}>{notifications_count}</span> : null}
                                                        <i className="fa-duotone fa-solid fa-bell"></i>
                                                 </Dropdown.Toggle>
                                                 <Dropdown.Menu className="p-0">
                                                        <Suspense fallback={<></>}>
                                                               <Notifications notificationList={notificationList} onNotificationToggle={this.onNotificationToggle} />
                                                        </Suspense>
                                                 </Dropdown.Menu>
                                          </Dropdown>
                                          <Suspense fallback={<></>}><UserDropdown navigate={navigate} authorized={authorized} /></Suspense>
                                   </div>
                            </div>
                     </div>
              </Navbar>
       }
}

const mapStateToProps = (state) => {
       return {
           notificationList: state.notification.notificationList
       }
   }
   export default connect(mapStateToProps)(Header);